import React,{Component} from 'react'
import {observer,inject} from 'mobx-react'
import {Input} from "antd";

@inject('StoreOrder')
@observer
class DropDownPlat extends Component{
    render(){
        const {StoreOrder} = this.props;
        return (
            <label className={"OrderItem"}>
                <span>
                    
                </span>
                {
                    StoreOrder.InputBox.platform === ""||
                    StoreOrder.InputBox.platform === ""||
                    StoreOrder.InputBox.platform === ""||
                    StoreOrder.InputBox.platform === ""||
                    StoreOrder.InputBox.platform === ""?
                    <select
                        value={StoreOrder.InputBox.platform}
                        onChange={(e)=>StoreOrder.handleInputBoxInput("platform",e.target.value)}
                    >
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                    :
                    <span>
                        <Input
                            value={StoreOrder.InputBox.platform}
                            onChange={(e)=>StoreOrder.handleInputBoxInput("platform",e.target.value)}
                            width={100}
                        />
                    </span>
                }
            </label>
        )
    }
}

export default DropDownPlat

